<template>
  <Header :title="$t('notice.title')" :is-back-button="false" />
  <NoItem :text="$t('notice.title')" v-if="!chatRoom?.length && !notices?.length">
    <font-awesome-icon :icon="bellIcon" />
  </NoItem>
  <div class="wrap" v-else>
    <div class="message-top">
      <h3>{{ $t('notice.message') }}</h3>
      <router-link to="/notice-detail">{{ $t('notice.more') }}</router-link>
    </div>
    <div class="message-wrap">
      <router-link
        :to="{ name: 'NoticeMessage', params: { id: message.id } }"
        v-for="(message, index) in chatRoom"
        :key="index"
        class="message-link"
      >
        <img :src="message.Course.Instructor.icon" alt="" class="message-image" />
        <span class="message-tag">{{ message.Course.Category.name }}</span>
        <p class="message-teacher-name">{{ truncate(message.Course.Instructor.name, 6) }}</p>
      </router-link>
    </div>

    <h3 class="notice-heading">{{ $t('notice.list') }}</h3>
    <div v-for="(notice, index) in notices" :key="index" class="notice-item">
      <img src="@/assets/TAKUMI_Logo_Vertical.png" alt="" class="icon" />
      <p class="notice-message">{{ notice.title }}</p>
      <font-awesome-icon :icon="arrowRight" class="arrow-icon" />
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed } from 'vue'
import { useQuery, useResult } from '@vue/apollo-composable'
import notice from '@/graphql/queries/notice'
import { NoticeQuery } from '@/graphql/@types/types'
import Header from '@/components/Header.vue'
import NoItem from '@/components/NoItem.vue'
import { faChevronRight } from '@fortawesome/free-solid-svg-icons'
import { useStore } from 'vuex'
import truncate from '@/filters/truncate'
import { faBell } from '@fortawesome/free-regular-svg-icons'

export default defineComponent({
  components: {
    Header,
    NoItem
  },
  setup() {
    const store = useStore()

    const { result } = useQuery<NoticeQuery>(notice, {
      student_id: store.state.currentUser?.uid
    })

    const chatRoom = useResult(result, null, (data) => data.ChatRooms)
    const notices = useResult(result, null, (data) => data.Notices)

    const arrowRight = computed(() => faChevronRight)
    const bellIcon = computed(() => faBell)

    return {
      chatRoom,
      notices,
      bellIcon,
      arrowRight,
      truncate
    }
  }
})
</script>

