
import { defineComponent, computed } from 'vue'
import { useQuery, useResult } from '@vue/apollo-composable'
import notice from '@/graphql/queries/notice'
import { NoticeQuery } from '@/graphql/@types/types'
import Header from '@/components/Header.vue'
import NoItem from '@/components/NoItem.vue'
import { faChevronRight } from '@fortawesome/free-solid-svg-icons'
import { useStore } from 'vuex'
import truncate from '@/filters/truncate'
import { faBell } from '@fortawesome/free-regular-svg-icons'

export default defineComponent({
  components: {
    Header,
    NoItem
  },
  setup() {
    const store = useStore()

    const { result } = useQuery<NoticeQuery>(notice, {
      student_id: store.state.currentUser?.uid
    })

    const chatRoom = useResult(result, null, (data) => data.ChatRooms)
    const notices = useResult(result, null, (data) => data.Notices)

    const arrowRight = computed(() => faChevronRight)
    const bellIcon = computed(() => faBell)

    return {
      chatRoom,
      notices,
      bellIcon,
      arrowRight,
      truncate
    }
  }
})
