import { gql } from '@apollo/client/core'

const notice = gql`
  query Notice($student_id: uuid) {
    ChatRooms(where: { student_id: { _eq: $student_id } }) {
      id
      Course {
        Instructor {
          name
          icon
        }
        Category {
          name
        }
      }
    }
    Notices(order_by: { created_at: desc }) {
      title
    }
  }
`

export default notice
